/*
Umit: Decided to use Flexbox here and rest of the project in trade of losing some compatibility.
*/
.App {
    /*background: #FF0000;*/
    width: 100vw;
    height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.App-top-bar {
    /*background: #FF0000;*/
    width: 100vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-line-pack: center;
        align-content: center;
}

/*
Umit: With these spatial attributes we can create a responsive square which can work when
width > height and height < width. I am not sure about rendering or painting cost for now but pretty sure it is better
than programmatically, dynamically update those attributes.
*/
.App-square {
    /*background: #0000FF;*/
    width: 90vw;
    height: 90vh;
    max-width: 90vh;
    max-height: 90vw;
    margin: auto auto;
}

.App-game-board {
    background: #DEDEDE;
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.App-game-board-row {
    /*background: #0000FF;*/
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.App-game-board-column {
    background: #FFFFFF;
    -ms-flex-positive: 1;
        flex-grow: 1;
    margin: 1px;
}




